    function getMaxValueObject(arr, prop) {
        return arr.reduce(function (prev, current) {
            return (prev[prop] > current[prop]) ? prev : current
        })
    }





    async function validaDataEtapa(cdAviso, cdEtapaAcao, data) {
        let retorno;
        try {
            const params = {
                cdAviso: cdAviso,
                cdEtapaAcao: cdEtapaAcao,
                data: data
            }
            const request = await API.post(`/validaDataEtapa`, params);
            retorno = request.data;
        } catch (error) {
            console.log(error)
        }
        return retorno;
    }


