    const acaoBotao = async (etapaAcao) => {

        // Se o botão clicado for o de "Saída da Sala"
        if (etapaAcao === 110) {
            if ((Paciente[0].PROX_ETAPA === etapaAcao) || (Paciente[0].CD_ETAPA === 110)) {
                // se existir pendência
                const buscaPendencia = Etapas.filter(x => x.DT_ETAPA === null && x.ETAPA_ACAO < 110);
                if (buscaPendencia.length > 0) {
                    setModalPendenciaEtapa(true);
                    setEtapasPendentes(buscaPendencia)
                    return false;
                }
                setModalSaidaSala(true);
                return false;
            }
        }

      
        // Se a etapa onde está sendo clicado o botão já houver data, retorna falso.
        if( (Etapas.filter(x => x.ETAPA_ACAO === etapaAcao)[0].DT_ETAPA !== null)){
            return false;
        }
        




        // Se estiver realizando uma ação, sem ter o usuário responsável, mostrará uma modal para o usuário inserir um novo usuário responsável.
        if (responsavelRecebimento.USUARIO === 'PENDENTE') {
            setModalUsuarioReponsavel(true)
            return false;
        }

        // Validação de datas superiores ou inferiores a outras etapas.
        const validaData = await validaDataEtapa(Paciente[0].COD_AVISO, etapaAcao, new Date())
        if ((validaData[0].RETORNO) !== 'S') {
            setModalValidacao(true)
            //document.getElementById(`${etapaAcao}`).value = ""
            setTextoValidacao(validaData[0].RETORNO)
            return false;
        }

        await avancarEtapaCirurgia(etapaAcao, auxData.length > 0 ? new Date(auxData) : new Date())
        await retornaDados()
        setAuxData("")
        return true;

    }


