        // eslint-disable-next-line prefer-template
        const minutoFormatado = minuto < 10 ? '0' + minuto : minuto;
        // eslint-disable-next-line prefer-template
        return horaFormatada + ' horas e ' + minutoFormatado + ' minutos';
