import API from '../Services';


const session = JSON.parse(localStorage.getItem('CAPP-local'))


async function desfazerEtapaPaciente(dadosPaciente, responsavel) {

    const dataTime  = new Date
    const moment    = new Date(dataTime.getTime())


    let retorno;

    try {
        const params = {
            avisoCirurgia: dadosPaciente.COD_AVISO,
            dtEtapa: moment,
            cdEtapa: dadosPaciente.CD_ETAPA,
            usuario: responsavel,
            salaCirurgia: dadosPaciente.CD_SAL_CIR,
            tipoAcao: 'D',
            sublocalidade: session.CODIGO_SUBLOCALIDADE
        }

        const desfazerEtapa = await API.post('/avancarEtapasCirurgia', params);
        if (desfazerEtapa) {
            retorno = true;
        }
    } catch (error) {
        console.log(error)
        retorno = false;
    }

    return retorno;
};



export default desfazerEtapaPaciente;
